@import 'Styles/theme.scss';

.collection-detail {
    width: 100%;
    background: url('/Assets/collection-bg.png') no-repeat;
    background-size: 100% 600px;
    .detail-info {
        margin: 70px auto;
        width: 1200px;
        .collection-detail-header {
            padding-top: 60px;
            display    : flex;
    
            .collection-detail-logo {
                .collection-logo {
                    border-radius: 50%;
                    width        : 130px;
                    height       : 130px;
                }
            }
    
            .collection-detail-info {
                padding        : 0 30px;
                width          : 650px;
                display        : flex;
                justify-content: center;
                flex-direction : column;
    
                .collection-detail-name {
                    font-size    : 20px;
                    font-family  : HiraginoSansGB, HiraginoSansGB-Regular;
                    font-weight  : 400;
                    color        : $lightBlack;
                    margin-bottom: 20px;
                }
    
                .collection-detail-desc {
                    font-size         : 14px;
                    font-family       : PingFangSC, PingFangSC-Regular;
                    font-weight       : 400;
                    color             : $lightBlack;
                    width             : 681px;
                    // opacity           : 0.5;
                    // background        : linear-gradient(180deg, #000000, rgba(180, 180, 180, 0.16) 80%, rgba(196, 196, 196, 0.09) 89%, rgba(216, 216, 216, 0.00));
                    // overflow          : hidden;
                    // display           : -webkit-box;
                    // -webkit-line-clamp: 3;
                    // -webkit-box-orient: vertical;
                    // text-overflow     : ellipsis;
                    // white-space       : normal;
                }
            }
        }
    
        .collection-edit-btn {
            width        : 120px;
            height       : 50px;
            background   : $white;
            border       : 1px solid $darkBlue;
            border-radius: 10px;
            line-height  : 50px;
            text-align   : center;
            font-size    : 14px;
            font-family  : HiraginoSansGB, HiraginoSansGB-Regular;
            color        : $darkBlue;
            margin       : 20px 0 0 160px;
            cursor       : pointer;
        }
    
        .pro-items {
            width          : 1200px;
            margin         : 100px auto 50px;
            display        : flex;
            justify-content: flex-start;
            flex-wrap      : wrap;
        }
    }
}