@import 'Styles/theme.scss';

.production-item {
    width        : 282px;
    height       : 390px;
    background   : $white;
    border       : 1px solid rgba(0, 0, 0, 0.10);
    border-radius: 10px;
    margin       : 0 24px 24px 0;
    text-align   : left;
    padding      : 15px 10px;
    font-size    : 16px;
    font-family  : HiraginoSansGB, HiraginoSansGB-W3;
    color        : $lightBlack;

    &:hover {
        box-shadow: 0px 0px 20px 0px rgba(96, 128, 180, 0.30);
    }

    &:hover .collection-item-edit-btn {
        display: block;
    }

    &:nth-child(4n) {
        margin-right: 0;
    }

    .production-item-create {
        a {
            color: $lightBlack;
        }
    }

    .production-works {
        width          : 262px;
        height         : 282px;
        display        : flex;
        justify-content: center;
        align-items    : center;
        cursor         : pointer;

        .production-works-img {
            max-width : 28vh;
            max-height: 28vh;
        }
    }
    .production-works-name {
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .production-item-price {
        display        : flex;
        justify-content: space-between;
        align-items    : center;
        text-shadow    : 0px 0px 20px 0px rgba(96, 128, 180, 0.30);
        font-family    : HiraginoSansGB, HiraginoSansGB-W6;
        font-weight    : 600;

        .production-standard {
            .production-button {
                border    : none;
                box-shadow: none;
                padding   : 0;
                height    : 0;
            }
        }
    }
}